.profile__title {
  max-width: 294px;
  font-size: 42px;
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  line-height: 48px;
  margin: 0;
  color: white;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 768px) {
  .profile__title {
    max-width: 189px;
    font-size: 27px;
    line-height: 33px;
  }
}