.popup__close {
  width: 32px;
  height: 32px;
  position: absolute;
  top: -40px;
  right: -40px;
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
}
.popup__close:hover {
  opacity: .6;
}

@media screen and (max-width: 550px) {
  .popup__close  {
   width: 25px;
   height: 25px;
   top: -35px;
   right: -20px;
  }
}

@media screen and (max-width: 420px) {
  .popup__close  {
      width: 20px;
      height: 20px;
      top: -36px;
      right: 0;
  }
}
